import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { FilterComponent } from '../../types/filter.types';

@Component({
	selector: 'aui-input-filter',
	template: `<div class="a-input has-icon-right aui-input-filter">
	<ng-container *ngIf="filter">
		<div class="a-input__wrapper">
			<input type="text" [placeholder]="filter.name" [(ngModel)]="value" (ngModelChange)="onFilter(value)">
			<span class="fa fa-search"></span>
		</div>
	</ng-container>
</div>
`,
})
export class InputFilterComponent implements OnInit, FilterComponent {
	@Input() filter;
	@Output() update = new EventEmitter();
	public value;

	ngOnInit() {
		if (this.filter) {
			this.value = this.filter.value;
		}
	}

	public onFilter(value) {
		this.update.emit(value);
	}
}
